@import url("https://fonts.googleapis.com/css2?family=Playfair+Display&family=Zilla+Slab+Highlight&display=swap");

body {
  font-size: 40px;
  color: black;
  font-weight: 300;
  text-align: center;
  background-color: grey;
}
h1 {
  font-weight: 300;
  margin: 0px;
  padding: 20px;
  font-size: 20px;
  background-color: black;
  color: white;
}
.question {
  font-size: 30px;
  margin-bottom: 10px;
}
.answers {
  margin-bottom: 20px;
  text-align: left;
  display: inline-block;
}
.answers label {
  display: block;
  margin-bottom: 10px;
}
button {
  font-size: 22px;
  background-color: green;
  color: white;
  border: 0px;
  border-radius: 3px;
  padding: 20px;
  cursor: pointer;
  margin-bottom: 20px;
}
button:hover {
  background-color: green;
}

//fades slides in n out for sit on top of one anohter
.slide {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s;
}
.active-slide {
  opacity: 1;
  z-index: 2;
}
.quiz-container {
  position: relative;
  height: 200px;
  margin-top: 40px;
}
